.filterGrid {
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 20px;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  transform: translateX(30px);
}

.filterItem {
  width: 200px;
  height: 100px;
  border: 1px solid lightgray;
  border-radius: 8px;
  box-shadow: 0px 3px 3px 1px rgba(0, 0, 0, 0.46);
  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  font-size: 22px;
  font-weight: 600;
  color: white;
  text-shadow: 1px 1px 2px black;
  box-shadow: 0px 8px 22px 11px rgba(255, 255, 255, 0.45) inset;
  background: linear-gradient(
    90deg,
    rgba(0, 0, 46, 0.8) 0%,
    rgba(9, 9, 56, 0.4) 40%,
    rgba(35, 13, 24, 0.3) 100%
  );
}
.filterItem:hover,
.filterItem:focus {
  transform: scale(1.03);
  box-shadow: 0px 7px 21px 10px rgba(255, 255, 255, 0.5) inset,
    2px 2px 4px -1px rgba(0, 0, 0, 0.3);
}
